import helper from '../../common/helper.js';
var _self;
export default {
	data() {
		lists: [],
		self = this
		return {
			mobile: 15115828895,
			password: 123456,
			openids: '',
			openid: ''
		};
	},
	onLoad: function() {
		// _self = this;
		// if (helper.islogin_Mer()) {
		// 	uni.switchTab({
		// 		url: '../Merchant/MerIndex'
		// 	})
		// }
	},
	methods: {
		openregister() { //打开注册页面
			uni.navigateTo({
				url: 'MerRegister',
			});
		},
		openphonelogin() { //打开手机短信短信登录页面 
			uni.navigateTo({
				url: 'MerPhoneLogin',
			});
		},
		MerchabtBut() {           //打开会员登录页面
			uni.navigateTo({
				url: 'login'
			});
		},
		loginbtn() { //手机登录按钮
			if (this.mobile == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入登录手机号',
					duration: 2000,
				});
				return;
			}
			if (!helper.checkmobile(this.mobile)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号码',
					duration: 2000,
				});
				return;
			}
			if (this.password.length < 6 || this.password.length > 12) {
				uni.showToast({
					icon: 'none',
					title: '请输入6-12个字符的密码',
					duration: 2000,
				});
				return;
			}
			uni.showLoading({
				mask: true,
				title: '登录中，请稍候'
			});
			uni.request({
				url: helper.websiteUrl + 'remerchant/LoginByPhone',
				data:{
					loginMark: helper.getloginMark(),
					token: '',
					data: '{"phone": "' + self.mobile + '","password": "' + self.password + '","type":"2"}'
				},
				method: 'GET',
				dataType: 'json',
				success: (res) => {
					if (res.data.code == 200) {
						var userinfo = res.data.data.baseinfo;
						uni.setStorageSync("token", userinfo.token);
						helper.setstate(userinfo.userId,self.password, userinfo.mobile, userinfo.nickName, 2);
						uni.showToast({
							title: '登录成功'
						});
						uni.reLaunch({
							url: '../Merchant/MerIndex'
						});
					} else {
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					uni.hideLoading();
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			});
		},

		openpage() {
			uni.navigateTo({
				url: '../Merchant/MerResetpass',
			});
		}
	}
}
